/* 1. 主标题渐变色和阴影 */
h1, .title {
 background: linear-gradient(90deg, #00d2ff 0%, #3a47d5 100%);
 -webkit-background-clip: text;
 -webkit-text-fill-color: transparent;
 text-shadow: 2px 2px 8px rgba(0, 210, 255, 0.3);
 font-family: 'Playfair Display', serif;
 }
 
 /* 2. 副标题加淡色底和圆角 */
 .subtitle, .page-subtitle {
 background: #f3f6fa;
 border-radius: 8px;
 padding: 8px 16px;
 display: inline-block;
 margin-bottom: 20px;
 font-size: 1.1em;
 }
 
 /* 3. 头像加圆形边框和阴影 */
 .profile img, .profile .profile-pic, .profile-pic {
 border: 4px solid #91eac9;
 box-shadow: 0 4px 24px rgba(127,127,213,0.18);
 transition: transform 0.3s;
 }
 .profile img:hover, .profile .profile-pic:hover, .profile-pic:hover {
 transform: scale(1.05) rotate(-2deg);
 }
 
 /* 4. 个人简介区块卡片化 */
 .profile {
 background: #fff;
 border-radius: 16px;
 box-shadow: 0 2px 16px rgba(134,168,231,0.08);
 padding: 24px;
 margin-bottom: 32px;
 }
 
 /* 5. 页面整体渐变背景 */
 body {
 background: linear-gradient(120deg, #f3f6fa 0%, #e9f7f6 100%);
 font-family: 'Merriweather', serif;
 }
 
 /* 6. 链接和按钮 hover 效果 */
 a, .btn, .btn-sm {
 transition: color 0.2s, background 0.2s, box-shadow 0.2s;
 }
 a:hover, .btn:hover, .btn-sm:hover {
 color: #7f7fd5 !important;
 background: #e9f7f6 !important;
 box-shadow: 0 2px 8px rgba(127,127,213,0.12);
 }

 /* 7. Education section styling */
 .education {
 margin-top: 2rem;
 }

 .education h2 {
 margin-bottom: 1rem;
 font-family: 'Playfair Display', serif;
 }

 .education ul {
 list-style: none;
 padding-left: 0;
 }

 .education ul li {
 margin-bottom: 1rem;
 }

 /* 8. About page layout */
 .about-layout {
 display: flex;
 flex-wrap: wrap;
 }

 .profile-column {
 flex: 1 1 300px;
 margin-right: 2rem;
 }

 .content-column {
 flex: 2 1 600px;
 }

 .post-content h3 {
 font-family: 'Playfair Display', serif;
 }
